(function() {
  'use strict';

  angular
    .module('angularProjects')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creationDate" is available by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('angularProjects')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('angularProjects')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function () {
    'use strict';

    angular
      .module('angularProjects')
      .controller('RealTimeController', RealTimeController);

    /** @ngInject */
    function RealTimeController($http) {
      var vm = this;

       $http({
         method: 'GET',
         url: 'http://flow-monitor:5000/realtime'
       }).then(function successCallback(response) {
         vm.data[0].values = [];
         for (var i = 0; i < response.data["samples"].length; i++) {
           vm.data[0].values.push({
             label: response.data["samples"][i]["ip"],
             value: response.data["samples"][i]["pps"]
           });
         }
       });




      vm.options = {
        chart: {
          type: 'discreteBarChart',
          height: 450,
          width: 1000,
          margin: {
            top: 20,
            right: 20,
            bottom: 50,
            left: 70
          },
          x: function (d) {
            return d.label;
          },
          y: function (d) {
            return d.value;
          },
          showValues: true,

          valueFormat: function (d) {
            return d3.format('d')(d);
          },
          duration: 500,
          xAxis: {
            axisLabel: 'flows',
            rotateLabels: -30
          },
          yAxis: {
            axisLabel: 'pps',
            tickFormat: function (d) {
              return d3.format('d')(d)
            },
          }
        }
      };

      vm.data = [
        {
          key: "",
          values: []
        }
      ]


      setInterval(function () {

        $http({
          method: 'GET',
          url: 'http://flow-monitor:5000/realtime'
        }).then(function successCallback(response) {
          if (response.data["samples"].length === 0) {
            return
          }
          ;

          // this callback will be called asynchronously
          // when the response is available
          console.log("GET OK");
          vm.data[0].values = [];

          for (var i = 0; i < response.data["samples"].length; i++) {
            vm.data[0].values.push({
              label: response.data["samples"][i]["ip"],
              value: response.data["samples"][i]["pps"]
            });
          }

        }, function errorCallback(response) {
          // called asynchronously if an error occurs
          // or server returns response with an error status.
          console.log("GET ERROR");
        });

      }, 4000);

      return vm;
    }
  })();

(function() {
  'use strict';

  angular
    .module('angularProjects')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, webDevTec, toastr) {
    var vm = this;

    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.creationDate = 1493813261780;
    vm.showToastr = showToastr;

    activate();

    function activate() {
      getWebDevTec();
      $timeout(function() {
        vm.classAnimation = 'rubberBand';
      }, 4000);
    }

    function showToastr() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = '';
    }

    function getWebDevTec() {
      vm.awesomeThings = webDevTec.getTec();

      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    }
  }
})();

(function () {
  'use strict';

  angular
    .module('angularProjects')
    .controller('HistoryController', HistoryController);

  /** @ngInject */
  function HistoryController($scope, $http) {
    var vm = this;

    vm.ips = [];
    vm.data = [{values: [], key: 'selected flow'}];


    $http({
      method: 'GET',
      url: 'http://flow-monitor:5000/history'
    }).then(function successCallback(response) {
      // this callback will be called asynchronously
      // when the response is availabl
      console.log("GET OK");
      vm.ips = response.data["ip_list"];
    }, function errorCallback(response) {
      // called asynchronously if an error occurs
      // or server returns response with an error status.
      console.log("GET ERROR");
    });


    vm.getSelectedText = function () {
      if (vm.selectedItem !== undefined) {
        return vm.selectedItem;
      } else {
        return "Please select a flow";
      }
    };

    vm.options = {
      chart: {
        type: 'lineChart',
        height: 400,
        width: 1000,
        yDomain: [0, 10000],
        margin: {
          top: 20,
          right: 20,
          bottom: 40,
          left: 55
        },
        x: function (d) {
          if (d!== undefined) {return d.x} else {return null};
        },
        y: function (d) {
          if (d!== undefined) {return d.y} else {return null};
        },
        useInteractiveGuideline: true,
        duration: 0,
        yAxis: {
          tickFormat: function (d) {
            return d3.format('1f')(d);
          }
        }
      }
    };





    setInterval(function () {
      if (vm.x_min === undefined) {vm.xmin = 0};

      if (vm.selectedItem !== undefined) {
        var lastTS;
        if (vm.data[0].values.length != 0) {
          lastTS = vm.data[0].values[vm.data[0].values.length-1]["x"]+ vm.x_min;
        }
        else {
          lastTS = 0;
        }

        $http({
          method: 'GET',
          url: 'http://flow-monitor/flowhistory?ip='+vm.selectedItem+'&ts='+lastTS
      }).then(function successCallback(response) {
          if (response.data["samples"].length === 0) {return};

          // this callback will be called asynchronously
          // when the response is available
          console.log("GET OK");
          if (vm.x_min === 0) {vm.x_min = response.data["samples"][0]["ts"]};
          for (var i = 0; i < response.data["samples"].length; i++) {
            vm.data[0].values.push({x:response.data["samples"][i]["ts"]-vm.x_min, y:response.data["samples"][i]["pps"]});
            if (vm.data[0].values.length > 30) vm.data[0].values.shift();
          }

          var ymax = 0;
          for (var i= 0; i<vm.data[0].values.length; i++) {
            if (vm.data[0].values[i].y > ymax) {
              ymax = vm.data[0].values[i].y;
            }
          }
          vm.options.chart.yDomain=[0,ymax+(ymax*.1)];

        }, function errorCallback(response) {
          // called asynchronously if an error occurs
          // or server returns response with an error status.
          console.log("GET ERROR");
        });
      } else {
        return "Please select a flow";
      }

    }, 2000);

    vm.onSelectedFlowChange = function (){
      vm.x_min = 0;
      vm.data[0].values = [];
    };
  }
})();

